/* Login Container */
.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
  }
  
  .login-container input {
    margin: 5px;
    padding: 10px;
    width: 200px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .login-container button {
    padding: 10px 20px;
    margin-top: 10px;
    background-color: #3f51b5;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .login-container button:hover {
    background-color: #303f9f;
  }
  
  /* Navbar */
  .navbar {
    display: flex;
    justify-content: space-around;
    background-color: #3f51b5;
    padding: 10px;
  }
  
  .navbar button {
    color: white;
    background: none;
    border: none;
    font-size: 16px;
    cursor: pointer;
  }
  
  .navbar button:hover {
    text-decoration: underline;
  }
  
  /* Main Content */
  .content {
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  